import React, { useContext, useEffect } from "react";
import { Row, Col } from "reactstrap";
import { CartContext } from "./CartContext";
// import Table from "../common/table";
import OrderForm from "../forms/OrderForm";

const Cart = () => {
  const [cart, setCart] = useContext(CartContext);

  // const totalPrice = cart.reduce((acc, curr) => acc + curr.price, 0);
  const title = cart.reduce((acc, curr) => curr.name, "");
  const Id = cart.reduce((acc, curr) => curr.Id, 0);
  const Count = cart.reduce((acc, curr) => curr.Count, 0);
  const Price = cart.reduce((acc, curr) => curr.price, 0);
  let titleArray = [];
  useEffect(() => {
    const list = [title];
    list.push(title);
    titleArray = [...list];
    console.log("Cart -> titleArray", titleArray);
  }, [title, titleArray]);
  return (
    // eslint-disable-next-line react/jsx-filename-extension
    <>
      <div>
        <Row style={{ marginBottom: 15 }}>
          <Col className="img-fluid">
            <b>
              <span className="chead-icon-length">Items : {cart.length + Count === 1 ? 0 : Count}</span>
            </b>
          </Col>
          <Col>
            <b>
              <span>{title}</span>
            </b>
          </Col>
          <Col>
            <b>
              <span> PKR {Price}</span>
            </b>
          </Col>
          <Col>
            <b>
              <span>Total PKR {Price * Count}</span>
            </b>
          </Col>
        </Row>
      </div>
      <div>
        <Row>
          <Col>
            <OrderForm Id={Id} title={title} Price={Price * Count} />
          </Col>
        </Row>
      </div>
    </>
  );
};
export default Cart;
