import React, { useContext, useState } from "react";
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Card,
  CardBody,
  CardHeader,
  CardImg,
  ButtonToolbar,
  ButtonGroup,
  Col
} from "reactstrap";
import { CartContext } from "./CartContext";

// import Counter from "./Counter";

const CaratModel = props => {
  const { buttonLabel, className } = props;

  const [modal, setModal] = useState(false);
  const [count, setcount] = useState(0);

  const toggle = () => setModal(!modal);

  const [cart, setCart] = useContext(CartContext);
  const increaseCount = () => {
    setcount(count + 1);
  };

  const decrementCount = () => {
    setcount(count - 1);
  };
  const addToCart = () => {
    const Items = {
      Id: props.Id,
      name: props.Title,
      price: props.Price,
      Count: count
    };
    setCart(currentState => [...currentState, Items]);
  };
  return (
    <div>
      <Button color="danger" onClick={toggle}>
        {buttonLabel}
      </Button>
      <Modal isOpen={modal} toggle={toggle} className={className}>
        <ModalHeader toggle={toggle}>{props.Title}</ModalHeader>
        <ModalBody>
          <Card>
            <CardHeader>
              <CardImg top src={props.src} alt="Card image cap" />
            </CardHeader>
            <CardBody>
              <b>PKR {props.Price}</b>
            </CardBody>
          </Card>
        </ModalBody>
        <ModalFooter>
          <div>
            <Col>
              <b>PKR {count * props.Price}</b>
            </Col>
          </div>
          <ButtonToolbar>
            <ButtonGroup className="btn btn-white">
              <Button className="btn btn-danger btn-outline-danger" onClick={() => decrementCount()}>
                <b>-</b>
              </Button>
              <div className="btn btn-outline-black disabled">{count}</div>
              <Button className="btn btn-danger btn-outline-danger" onClick={() => increaseCount()}>
                <b>+</b>
              </Button>
            </ButtonGroup>
          </ButtonToolbar>
          <Button color="primary" onClick={() => addToCart()}>
            Add to cart
          </Button>
          <Button color="secondary" onClick={toggle}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
    </div>
  );
};

export default CaratModel;
